.b-breadcrumbs {
    @extend .u-wrapper;
    padding-bottom: 20px;

    a,
    span {
        margin-top: -2px;
        display: inline-block;
        vertical-align: top;
    }

    span,
    .material-icons { color: $white-40; }
    a { color: $white-90; }
    a:hover {text-decoration: underline;}
}
